import React, { useState } from 'react';
import { Card, CardActions, CardContent, CardMedia, Button, Typography, ButtonBase } from '@material-ui/core';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import DeleteIcon from '@material-ui/icons/Delete';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import ThumbUpAltOutlined from '@material-ui/icons/ThumbUpAltOutlined';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';
//Styles
import useStyle from './styles.js';
//redux
import { useDispatch } from 'react-redux';
import { deletePost, likePost } from '../../../redux/actions/postsActions';


const Post = ({ post, setCurrentId }) => {
    const classes = useStyle();
    const dispatch = useDispatch();
    const user = JSON.parse(localStorage.getItem('profile'));
    const navigate = useNavigate();
    const [likes, setLikes] = useState(post?.likes);

    const userId = user?.result?.googleId || user?.result?._id
    const hasLikePost = post.likes.find((like) => like === userId)

    const handlelikes = async () => {
        dispatch(likePost(post._id))

        if(hasLikePost) {
            setLikes(post.likes.filter((id) => id !== userId))
        }else {
            setLikes([...post.likes, userId])
        }
    }

    //todo: componente para agregar iconos de likes propio y de otros
    const Likes = () => {
        if (likes?.length > 0) {
            return likes.find((like) => like === (user?.result?.googleId || user?.result?._id))
                ? (
                    <><ThumbUpAltIcon fontSize="small" />&nbsp;{likes.length > 2 ? `You and ${likes.length - 1} others` : `${likes.length} like${likes.length > 1 ? 's' : ''}`}</>
                ) : (
                    <><ThumbUpAltOutlined fontSize="small" />&nbsp;{likes.length} {likes.length === 1 ? 'Like' : 'Likes'}</>
                );
        }

        return <><ThumbUpAltOutlined fontSize="small" />&nbsp;Like</>;
    };

    

    const openPost = (e) => {
        // dispatch(getPost(post._id, history));

        navigate(`/posts/${post._id}`);
    };

    return (
        <Card className={classes.card} raised elevation={6}>
            <ButtonBase component="span" name="test" onClick={openPost} className={classes.cardAction}>
                <CardMedia className={classes.media} image={post.selectedFile || 'https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png'} title={post.title} />
                <div className={classes.overlay}>
                    <Typography variant="h6">{post.name}</Typography>
                    <Typography variant="body2">{moment(post.createdAt).fromNow()}</Typography>
                </div>

                {(user?.result?.googleId === post?.creator || user?.result?._id === post?.creator) && (
                    <div className={classes.overlay2} name="edit">
                        <Button style={{ color: '#fff' }} size="small" onClick={(e) => { e.stopPropagation(); setCurrentId(post._id) }}>
                            <MoreHorizIcon fontSize="medium" />
                        </Button>
                    </div>
                )}
                <div className={classes.details}>
                    <Typography variant="body2" color='textSecondary' component="h2">{post.tags.map(tag => `#${tag}`)}</Typography>
                </div>
                <Typography className={classes.title} variant="h5" gutterBottom component="h2">{post.title}</Typography>
                <CardContent>
                    <Typography variant="body2" color="textSecondary" component='p'>{post.message.split(' ').slice(0,20).join(' ')}...</Typography>
                </CardContent>
            </ButtonBase>
            <CardActions className={classes.cardActions}>
                <Button size='small' color='primary' disabled={!user?.result} onClick={handlelikes}>
                    <Likes />
                </Button>
                {(user?.result?.googleId === post?.creator || user?.result?._id === post?.creator) && (
                    <Button size='small' color="secondary" onClick={() => dispatch(deletePost(post._id))}>
                        <DeleteIcon fontSize='small' />
                        &nbsp; Delete
                    </Button>
                )}
            </CardActions>
        </Card>
    )
}

export default Post